/**
 *  ,     
 * @property level -  ,  1  4
 * @property attack -  
 * @property defence -  
 * @property health -  
 * @property type -      :
 * swordsman
 * bowman
 * magician
 * daemon
 * undead
 * vampire
 */
export default class Character {
  constructor(level, type = 'generic') {
    this.level = level;
    this.health = 50;
    this.attack = 0;
    this.defence = 0;
    this.type = type;
    // TODO:  ,  -  "new Character()"
    if (new.target.name === 'Character') {
      throw new Error('     Character');
    }
  }

  levelUp() {
    if (this.level > 3) return;

    this.level += 1;
    this.attack = Math.floor(Math.max(this.attack, this.attack * (80 + this.health) * 0.01));
    this.defence = Math.floor(Math.max(this.defence, this.defence * (80 + this.health) * 0.01));

    this.health += 80;
    if (this.health > 100) {
      this.health = 100;
    }
  }
}
